.s-fin-pin-wrap{
    opacity: 0;
    position: absolute;
    top: calc(50% - 418.5px);
    left: calc(50% - 250px);
    width: 900px;
    height: 827px;
    z-index: 10;
    transition: 1s ease;

    .fin-zoning {
        position: absolute;
        width: 900px;
        height: 100%;
        top: 10px;
        left: 0;
        opacity: 0.2;
    }

    .s-fin-pin {
        position: absolute;
        &:hover {
            .area-mark {
                opacity: 1;
                transition: 0.3s ease;
            }
        }
        .area-mark {
            opacity: 0;
            transition: 0.3s ease;
            cursor: pointer;
            &:hover ~ .pin {
                background: orange;
            }
        }

        .pin {
            position: absolute;
            z-index: 50;            
            display: flex;
            align-items: center;
            transition: 0.5s ease;
            cursor: pointer;

            &:hover {
                .pin-title,
                .pin-icon,
                .pin-line {
                    opacity: 1;
                }
            }
            
            .pin-head {
                width: 8px;
                height: 8px;
                top: 1px;
                display: flex;
                justify-content: center;
                border-radius: 50%;
                transition: 0.3s ease;
                font-size: 10px;
                font-family: Helvetica-Bold;
                line-height: 22px;
                background: $cl-white;
                color: $cl-black;
                transition: 0.3s ease;
            }

            .pin-title {
                opacity: 0;
                bottom : 2px;
                width: max-content;
                padding: 0 6px;
                font-size: 10px;
                line-height: 14px;
                color: $cl-white;
                transition: 0.3s ease;
            }
            
            .pin-icon {
                opacity: 0;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 36px;
                height: 36px;
                border: 0.4px solid $cl-white;
                border-radius: 50%;
                background: $cl-dk-background;
                transition: 0.3s ease;

                img {
                    width: 24px;
                    height: 24px;
                }
            }

            .pin-line {
                opacity: 0;
                height: 0.2px;
                background: $cl-white;
                transition: 0.3s ease;
            }

            .show-pin-item {
                transition: 0.3s ease;
                opacity: 0.6;
            }
        }

        .hide-pin {
            opacity: 0;

            &:hover {
                opacity: 0;
            }
        }
        
        // premium area
        &:nth-child(1) { 
            top: 85px;
            left: 673.7px;
            width: 46px;
            .pin {
                top: 4px;
                left: 20px;
                .pin-line {
                    width: 240px;
                }
            }
        }

        // sauna & Restaurant
        &:nth-child(2) { 
            top: 186px;
            left: 691.5px;
            width: 46px;
            .pin {
                left: 14px;
                .pin-line {
                    width: 227px;
                }
            }
        }
        // meditation garden
        &:nth-child(3) { 
            top: 256.5px;
            left: 588px;
            width: 37px;
            z-index: 1;
            .pin {
                top: 0px;
                right: 14px;
                flex-direction: row-reverse;
                .pin-line {
                    width: 452px;
                }
            }
        }

        // cocktail & organic tea garden
        &:nth-child(4) {
            top: calc(266px);
            left: calc(682px); 
            width: 72px;
            .pin {
                left: 12px;
                .pin-line {
                    width: 238px;
                }
            }
        }

        // forest garden
        &:nth-child(5) { 
            top: calc(247px); 
            left: calc(516px); 
            width: 135px;
            .pin {
                top: 110px;
                left: 80px;
                .pin-line {
                    width: 336px;
                }
            }
        }

        // village & bbq deck
        &:nth-child(6) { 
            top: 340px;
            left: 527PX;
            width: 57px;
            .pin {
                flex-direction: row-reverse;
                top: 30px;
                right: 26px;
                .pin-line {
                    width: 399px;
                }
            }
        }

        // main centre
        &:nth-child(7) { 
            top: calc(419px); 
            left: calc(536PX);
            width: 31px;
            .pin {
                flex-direction: row-reverse;
                right: 12px;
                .pin-line {
                    width: 396px;
                }
            }
        }

        // play ground
        &:nth-child(8) { 
            top: calc(383px); 
            left: calc(431px); 
            width: 125px;
            .pin {
                flex-direction: row-reverse;
                top: 90px;
                right: 34px;
                .pin-line {
                    width: 363px;
                }
            }
        }

        // cocktail & organic tea garden
        &:nth-child(9) { 
            top: calc(385px); 
            left: calc(734px);
            width: 156px;
            .pin {
                top: 16px;
                left: 34px;
                .pin-line {
                    width: 164px;
                }
            }
        }
        
        // pet area
        &:nth-child(10) { 
            top: 540px;
            left: 542px;
            width: 122px;
            .pin {
                top: 120px;
                left: 42px;
                .pin-line {
                    width: 348px;
                }
            }
        }

        // furnicular
        &:nth-child(11) { 
            top: 575px; 
            left: 483px; 
            width: 27px;
            .pin {
                flex-direction: row-reverse;
                top: -4px;
                right: 12px;
                .pin-line {width: 340px;}
            }
        }
    }

    .s-fin-pin-selected {
        .pin {
                .pin-head {
                    background: orange;
                    color: #191919;
                }
        }

        .area-mark {
            opacity: 1;
        }
    }
}

