body {
    background: $cl-dk-background;
}

img {
    display: block;
    width: 100%;
}

.theme-changer {
    display: none;
    position: absolute;
    bottom: 72px;
    right: 48px;
    z-index: 9999;
    cursor: pointer;
}

.button {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 240px;
    padding: 12px 0;
    border-radius: 8px;
    box-shadow: 0px 8px 20px 5px #0002;
    background: rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(6px);
    transition: 0.3s ease;

    &:hover {
        i {
            right: 50px;
        }

        box-shadow: 0px 8px 20px 5px #0003;
        background: rgba(255, 255, 255, 0.7);
    }

    span {
        font-size: 14px;
        line-height: 20px;
        margin-right: 12px;
    }

    i {
        position: absolute;
        width: 18px;
        top: 13px;
        right: 54px;
        transition: 0.3s ease;
    }
}

.mobile-env {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 100%;
    height: 100vh;
    padding: 24px;
}