.modular-info {
    position: absolute;
    bottom: 120px;
    left: 48px;
    z-index: 100;
    opacity: 1;
    transition: 0.5s ease;
    table {
        display: block;
        font-size: 12px;
        text-align: center;
        tr {
            display: inline-block;
            display: flex;
            width: 380px;
            th {
                width: 30%;
                padding: 0 0 12px 0;
                display: inline-block;
                text-align: left;
                border-bottom: 0.4px solid $cl-white;
                color: $cl-dk-heading;
                &:last-child {
                    font-weight: bold;
                }
            }
            td {
                display: flex;
                align-items: center;

                width: 30%;
                padding: 8px 0;
                color: $cl-dk-body;
                img {
                    width: 32px;
                }
                &:last-child {
                    font-weight: bold;
                }
            }

            &:nth-child(2) {
                td {
                    &:last-child {
                        opacity: 0;
                    }
                }
            }
        }
    }
}

.hide-modular-info {
    opacity: 0;
    z-index: -10;
    transition: 0.5s ease;
}