.teaser {
    opacity: 1;
    position: absolute;
    width: 100%;
    height: 100vh;
    z-index: 999;
    background: $cl-dk-background;
    transition: 0.3s ease;
    overflow: auto;
    white-space: pre-wrap;
    cursor: auto;

    .teaser-container {
        position: relative;
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 100vh;
        margin: 0 auto;

        .teaser-nav-container {
            position: relative;
            display: flex;
            justify-content: center;
            gap: 16px;
            width: 100%;
            padding: 12px 0;
            
            .close-btn {
                position: absolute;
                width: 36px;
                height: 36px;
                top: calc(50% - 18px);
                right: 12px;
                z-index: 10;
            }

            .teaser-numbering {
                display: flex;
                justify-content: center;
                align-items: flex-end;
                gap: 16px;
                .btn {
                    width: 24px;
                    height: 24px;
                    opacity: 0.5;
                    transition: 0.3s ease;
                    cursor: pointer;
                    &:hover {
                        opacity: 1;
                    }
                }
                .num {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;
                    position: relative;
                    gap: 4px;
                    padding-bottom: 2px;
                    opacity: 0.5;
                    color: $cl-dk-heading;
                    transition: 0.3s ease;
                    cursor: pointer;

                    .marker {
                        width: 6px;
                        height: 6px;
                        border-radius: 50%;
                        background: $cl-dk-heading;
                        transition: 0.3s ease;
                        opacity: 0;
                    }
                    .show-marker {
                        opacity: 0.8;
                    }
                }
                .select-num {
                    opacity: 0.8;
                    font-weight: bold;
                }
            }
        }

        .teaser-content-wrap {
            position: relative;
            height: 100%;
            .teaser-content {
                display: flex;
                flex-direction: column;
                justify-content: center;
                gap: 24px;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                z-index: 10;
                padding: 0 3.75%;
                opacity: 0;
                transition: 0.3s ease;
                .content-caption {
                    display: flex;
                    flex-direction: column;
                    gap: 24px;
                    position: absolute;
                    top: 36px;
                    width: 30%;
                    .caption {
                        color: $cl-dk-body;
                        opacity: 0.7;
                    }
                    .caption-heading {
                        font-size: 24px;
                        color: $cl-dk-body;
                        opacity: 1;
                    }
                    .caption-body {
                        color: $cl-dk-body;
                        opacity: 0.7;
                    }
                }
                .content-heading {
                    width: 30%;
                    font-size: 48px;
                    line-height: 60px;
                    color: $cl-background;
                }
                .content-sub-heading {
                    width: 30%;
                    color: $cl-dk-heading;
                }
                .content-body {
                    width: 30%;
                    color: $cl-sub-background;
                }
                .content-image {
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    gap: 24px;
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    img {
                        width: 240px;
                    }
                    p {
                        color: $cl-white;
                        opacity: 0.7;
                    }
                }
            }
            .show-teaser-content {
                opacity: 1;
            }
            .image-wrap-block {
                position: absolute;
                top: 0;
                left: 0;
                z-index: -10;
                display: flex;
                justify-content: center;
                width: 100%;
                height: 100%;
                position: relative;
                overflow: hidden;
                background: #191919;
    
                .teaser-image {
                    position: absolute;
                    z-index: 10;
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    opacity: 0;
                    transition: 0.3s ease;
                }
    
                .show-teaser-image {
                    opacity: 1;
                }
            }
        }
    }
}

.teaser-hide {
    opacity: 0;
    z-index: -50;
    overflow: hidden;
}